'use strict';

import mongoose from 'mongoose';
import {registerEvents} from './contact.events';

var ContactSchema = new mongoose.Schema({
name:{
 	type:String
},
email:{
	type:String
},
message:{
	type:String
},
subject:{
	type:String
},
createdAt:{
	type:Date,
	default:Date.now
}
});

registerEvents(ContactSchema);
export default mongoose.model('Contact', ContactSchema);
